html, 
body, #root, .App {
    height: 100vh;
    width: 100vw;
}

.App::before {
  content: "";
  background: url('./assets/dawn-artwork.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  filter: brightness(0.5);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.button {
  -webkit-box-align: baseline;
  align-items: baseline;
  border-width: 0px;
  border-radius: 3px;
  box-sizing: border-box;
  display: inline-flex;
  font-size: inherit;
  font-style: normal;
  font-family: inherit;
  font-weight: 500;
  max-width: 100%;
  position: relative;
  text-align: center;
  text-decoration: none;
  transition: background 0.1s ease-out 0s, box-shadow 0.15s cubic-bezier(0.47, 0.03, 0.49, 1.38) 0s;
  white-space: nowrap;
  background: rgb(0, 82, 204);
  cursor: pointer;
  height: 2.28571em;
  line-height: 2.28571em;
  padding: 0px 10px;
  vertical-align: middle;
  width: auto;
  -webkit-box-pack: center;
  justify-content: center;
  color: rgb(255, 255, 255) !important;
}
.button-secondary {
  cursor: pointer;
  border: 1px solid black;
  border-radius: 3px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  transition: background 0.1s ease-out 0s, box-shadow 0.15s cubic-bezier(0.47, 0.03, 0.49, 1.38) 0s;
  white-space: nowrap;
  line-height: 2.28571em;
  padding: 0px 10px;
  vertical-align: middle;
  width: auto;
  color: rgb(66, 82, 110) !important;
}